export default {
	data() {
		return {
			space: ' ',
			searchText: '',
			selectCur: 0,
			selectItems: ['综合', '价格', '距离', '成色']
		}
	},
	onLoad(option) {
		this.searchText = JSON.parse(decodeURIComponent(option.text))
	},
	methods: {
		selectChange(e) {
			this.selectCur = e.currentTarget.dataset.id
		}
	}
}
